<template>
  <div class="cart">
    <h4>全部商品</h4>
    <div class="cart-main">
      <div class="cart-th">
        <div class="cart-th1">全部</div>
        <div class="cart-th2">商品</div>
        <div class="cart-th3">单价（元）</div>
        <div class="cart-th4">数量</div>
        <div class="cart-th5">小计（元）</div>
        <div class="cart-th6">操作</div>
      </div>
      <div class="cart-body">
        <ul class="cart-list" v-for="cart in cartInfoList" :key="cart.id">
          <li class="cart-list-con1">
            <input
              type="checkbox"
              name="chk_list"
              :checked="cart.isChecked == 1"
              @change="updateChecked(cart.skuId, $event.target.checked)"
            />
          </li>
          <li class="cart-list-con2">
            <img :src="cart.imgUrl" />
            <div class="item-msg">
              {{ cart.skuName }}
            </div>
          </li>
          <li class="cart-list-con4">
            <span class="price">{{ cart.skuPrice }}.00</span>
          </li>
          <li class="cart-list-con5">
            <a class="mins" @click="handler('minus', -1, cart)">-</a>
            <input
              autocomplete="off"
              type="text"
              :value="cart.skuNum"
              min="1"
              class="itxt"
              @change="handler('change', $event.target.value * 1, cart)"
            />
            <a class="plus" @click="handler('add', 1, cart)">+</a>
          </li>
          <li class="cart-list-con6">
            <span class="sum">{{ cart.skuNum * cart.skuPrice }}.00</span>
          </li>
          <li class="cart-list-con7">
            <a class="sindelet" @click="delSku(cart)">删除</a>
            <br />
            <a>移到收藏</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="cart-tool">
      <div class="select-all">
        <input
          class="chooseAll"
          type="checkbox"
          :checked="isAllCheck && cartInfoList.length > 0"
          @change="updateAllCartChecked"
        />
        <span>全选</span>
      </div>
      <div class="option">
        <a @click="deleteAllCheckedCart">删除选中的商品</a>
        <a>移到我的关注</a>
        <a>清除下柜商品</a>
      </div>
      <div class="money-box">
        <div class="chosed">已选择 <span>0</span>件商品</div>
        <div class="sumprice">
          <em>总价（不含运费） ：</em>
          <i class="summoney">{{ totalPrice }}</i>
        </div>
        <div class="sumbtn">
          <a class="sum-btn" @click="$router.push('/trade')">结算</a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
import debounce from 'lodash/debounce';
export default {
  name: 'ShopCart',
  mounted() {
    this.getData();
  },
  methods: {
    // 获取个人购物车的数据
    getData() {
      this.$store.dispatch('getCartList');
    },
    // 修改某一个产品的个数
    // 还需要节流进行限制用户重复点击

    delSku: function (cart) {
      this.$store
        .dispatch('deleteCartListBySkuId', cart.skuId)
        .then(result => {
          this.getData();
        })
        .catch(err => {
          throw err;
        });
    },
    // 加上防抖防止点击过快导致出现异常
    handler: debounce(function (type, disNum, cart) {
      // type为了区分你点击了哪个元素
      // 目前disNum形参：点击+，-，disNum都是变化了多少，而点击input，disNum是变化后的结果
      //cart是为了得到你点击元素中商品的skuId
      switch (type) {
        // 加号
        case 'add':
          disNum = 1;
          break;

        case 'minus':
          // 判断目前的商品数量大于1，才能使disNum = -1
          cart.skuNum > 1 ? (disNum = -1) : (disNum = 0);
          break;
        case 'change':
          if (
            isNaN(disNum) ||
            disNum < 1 ||
            disNum.toString().indexOf('.') != -1
          ) {
            alert('输入的数字不合法');
            disNum = 0;
          } else {
            disNum = disNum - cart.skuNum;
          }
          break;
        default:
          break;
      }
      this.$store
        .dispatch('addOrUpdateShopCart', {
          skuId: cart.skuId,
          skuNum: disNum,
        })
        .then(result => {
          this.getData();
        })
        .catch(err => {
          throw err;
        });
    }, 500),

    // 修改某个产品的勾选状态
    updateChecked(skuId, isChecked) {
      isChecked = isChecked ? 1 : 0;
      this.$store
        .dispatch('updateChecked', { skuId, isChecked })
        .then(result => {
          this.getData();
        })
        .catch(err => {
          throw err;
        });
    },
    // 删除全部选中的产品 因为这个函数没办法收集到一些有用的数据,所以我们调用别的函数来删除所以商品
    async deleteAllCheckedCart() {
      try {
        await this.$store.dispatch('deleteAllCheckedCart');
        this.getData();
      } catch (error) {
        throw error;
      }
    },
    async updateAllCartChecked(event) {
      let checked = event.target.checked ? '1' : '0';
      try {
        await this.$store.dispatch('updateAllChecked', checked);
        this.getData();
      } catch (error) {
        throw error;
      }
    },
  },
  computed: {
    ...mapGetters(['cartList']),
    cartInfoList() {
      return this.cartList.cartInfoList || [];
    },
    // 计算购买产品的总价
    totalPrice() {
      let sumPrice = 0;
      this.cartInfoList.forEach(element => {
        sumPrice += element.skuPrice * element.skuNum;
      });
      return sumPrice;
    },
    // 判断底部复选框是否全部勾选【全部产品选中，才勾选】
    isAllCheck() {
      // 所有item的isChecked全为1，返回true，否则返回false
      return this.cartInfoList.every(item => {
        return item.isChecked == 1;
      });
    },
  },
};
</script>

<style lang="less" scoped>
.cart {
  width: 1200px;
  margin: 0 auto;

  h4 {
    margin: 9px 0;
    font-size: 14px;
    line-height: 21px;
  }

  .cart-main {
    .cart-th {
      background: #f5f5f5;
      border: 1px solid #ddd;
      padding: 10px;
      overflow: hidden;

      & > div {
        float: left;
      }

      .cart-th1 {
        width: 25%;

        input {
          vertical-align: middle;
        }

        span {
          vertical-align: middle;
        }
      }

      .cart-th2 {
        width: 25%;
      }

      .cart-th3,
      .cart-th4,
      .cart-th5,
      .cart-th6 {
        width: 12.5%;
      }
    }

    .cart-body {
      margin: 15px 0;
      border: 1px solid #ddd;

      .cart-list {
        padding: 10px;
        border-bottom: 1px solid #ddd;
        overflow: hidden;

        & > li {
          float: left;
        }

        .cart-list-con1 {
          width: 15%;
        }

        .cart-list-con2 {
          width: 35%;

          img {
            width: 82px;
            height: 82px;
            float: left;
          }

          .item-msg {
            float: left;
            width: 150px;
            margin: 0 10px;
            line-height: 18px;
          }
        }

        .cart-list-con4 {
          width: 10%;
        }

        .cart-list-con5 {
          width: 17%;

          .mins {
            cursor: pointer;
            border: 1px solid #ddd;
            border-right: 0;
            float: left;
            color: #666;
            width: 6px;
            text-align: center;
            padding: 8px;
          }

          input {
            border: 1px solid #ddd;
            width: 40px;
            height: 33px;
            float: left;
            text-align: center;
            font-size: 14px;
          }

          .plus {
            cursor: pointer;
            border: 1px solid #ddd;
            border-left: 0;
            float: left;
            color: #666;
            width: 6px;
            text-align: center;
            padding: 8px;
          }
        }

        .cart-list-con6 {
          width: 10%;

          .sum {
            font-size: 16px;
          }
        }

        .cart-list-con7 {
          width: 13%;

          a {
            color: #666;
            cursor: pointer;
          }
        }
      }
    }
  }

  .cart-tool {
    overflow: hidden;
    border: 1px solid #ddd;

    .select-all {
      padding: 10px;
      overflow: hidden;
      float: left;

      span {
        vertical-align: middle;
      }

      input {
        vertical-align: middle;
      }
    }

    .option {
      padding: 10px;
      overflow: hidden;
      float: left;

      a {
        cursor: pointer;
        float: left;
        padding: 0 10px;
        color: #666;
      }
    }

    .money-box {
      float: right;

      .chosed {
        line-height: 26px;
        float: left;
        padding: 0 10px;
      }

      .sumprice {
        width: 200px;
        line-height: 22px;
        float: left;
        padding: 0 10px;

        .summoney {
          color: #c81623;
          font-size: 16px;
        }
      }

      .sumbtn {
        float: right;

        a {
          cursor: pointer;
          display: block;
          position: relative;
          width: 96px;
          height: 52px;
          line-height: 52px;
          color: #fff;
          text-align: center;
          font-size: 18px;
          font-family: 'Microsoft YaHei';
          background: #e1251b;
          overflow: hidden;
        }
      }
    }
  }
}
</style>
